import React from 'react';
import { Link } from 'react-router-dom';
import { motion } from 'framer-motion';
import { FiGithub, FiTwitter, FiLinkedin, FiInstagram } from 'react-icons/fi';
import { useTheme } from '../../../contexts/ThemeContext';

const socialLinks = [
  { icon: FiGithub, href: 'https://github.com', label: 'GitHub' },
  { icon: FiTwitter, href: 'https://twitter.com', label: 'Twitter' },
  { icon: FiLinkedin, href: 'https://linkedin.com', label: 'LinkedIn' },
  { icon: FiInstagram, href: 'https://instagram.com', label: 'Instagram' }
];

const quickLinks = [
  { path: '#hero', label: 'Home', sectionId: 'hero-section' },
  { path: '#how-it-works', label: 'How It Works', sectionId: 'how-it-works-section' },
  { path: '#features', label: 'Features', sectionId: 'features-section' },
  { path: '#digital-library', label: 'Digital Library', sectionId: 'digital-library-section' },
  { path: '#racks', label: 'Find Books', sectionId: 'racks-section' },
  { path: '#testimonials', label: 'Testimonials', sectionId: 'testimonials-section' },
  { path: '#faq', label: 'FAQ & Contact', sectionId: 'faq-section' }
];

const resources = [
  { path: '#features', label: 'Library Features', sectionId: 'features-section' },
  { path: '#digital-library', label: 'Digital Resources', sectionId: 'digital-library-section' },
  { path: '#racks', label: 'Book Finder', sectionId: 'racks-section' },
  { path: '#faq', label: 'Help Center', sectionId: 'faq-section' }
];

const legal = [
  { path: '/privacy', label: 'Privacy Policy' },
  { path: '/terms', label: 'Terms of Service' },
  { path: '/cookies', label: 'Cookie Policy' }
];

const Footer = ({ onLoad }) => {
  const { isDarkMode } = useTheme();
  
  // Handle smooth scrolling
  const handleNavClick = (e, sectionId) => {
    e.preventDefault();
    const section = document.getElementById(sectionId);
    if (section) {
      section.scrollIntoView({ behavior: 'smooth' });
    }
  };

  // Call onLoad when component mounts
  React.useEffect(() => {
    onLoad?.();
  }, [onLoad]);

  return (
    <footer className={`relative ${
      isDarkMode ? 'bg-gray-900 border-t border-white/10' : 'bg-gray-900'
    }`}>
      <div className="container mx-auto px-4 py-12">
        <div className="grid grid-cols-1 md:grid-cols-4 gap-12">
          {/* About Section */}
          <motion.div 
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.6 }}
            className="col-span-1 md:col-span-2"
          >
            <Link to="/">
              <h3 className="text-2xl font-normal text-white mb-4 bg-clip-text text-transparent bg-gradient-to-r from-primary via-primary to-primary/80 font-['Audiowide']">
                MATRIX
              </h3>
            </Link>
            <p className="text-gray-400 mb-6 max-w-md">
              Modern library management system designed to streamline operations and enhance the learning experience through digital innovation and smart organization.
            </p>
            <div className="flex gap-4">
              {socialLinks.map((social) => (
                <motion.a
                  key={social.label}
                  href={social.href}
                  target="_blank"
                  rel="noopener noreferrer"
                  className="w-10 h-10 rounded-lg bg-white/10 hover:bg-white/20 flex items-center justify-center text-gray-400 hover:text-white transition-all"
                  whileHover={{ scale: 1.1 }}
                  whileTap={{ scale: 0.9 }}
                >
                  <social.icon className="w-5 h-5" />
                </motion.a>
              ))}
            </div>
          </motion.div>

          {/* Quick Links */}
          <motion.div
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.6, delay: 0.1 }}
          >
            <h3 className="text-lg font-semibold text-white mb-6">Quick Links</h3>
            <ul className="space-y-3">
              {quickLinks.map((link) => (
                <motion.li
                  key={link.path}
                  whileHover={{ x: 5 }}
                >
                  <a 
                    href={link.path}
                    onClick={(e) => handleNavClick(e, link.sectionId)}
                    className="text-gray-400 hover:text-white transition-colors cursor-pointer"
                  >
                    {link.label}
                  </a>
                </motion.li>
              ))}
            </ul>
          </motion.div>

          {/* Resources */}
          <motion.div
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.6, delay: 0.2 }}
          >
            <h3 className="text-lg font-semibold text-white mb-6">Resources</h3>
            <ul className="space-y-3">
              {resources.map((link) => (
                <motion.li
                  key={link.path}
                  whileHover={{ x: 5 }}
                >
                  <a 
                    href={link.path}
                    onClick={(e) => handleNavClick(e, link.sectionId)}
                    className="text-gray-400 hover:text-white transition-colors cursor-pointer"
                  >
                    {link.label}
                  </a>
                </motion.li>
              ))}
            </ul>
          </motion.div>
        </div>

        {/* Bottom Section */}
        <motion.div 
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ duration: 0.6, delay: 0.3 }}
          className="mt-12 pt-8 border-t border-white/10 flex flex-col md:flex-row justify-between items-center gap-4"
        >
          <p className="text-gray-400 text-sm text-center md:text-left">
            Â© {new Date().getFullYear()} <span className="font-['Audiowide']">MATRIX</span> LMS. All rights reserved.
          </p>
          <div className="flex gap-6">
            {legal.map((link) => (
              <Link
                key={link.path}
                to={link.path}
                className="text-gray-400 hover:text-white text-sm transition-colors"
              >
                {link.label}
              </Link>
            ))}
          </div>
        </motion.div>
      </div>
    </footer>
  );
};

export default Footer; 